@import '../../variables';

.sobre-mim {
    text-align: center;
    margin-top: 92px;
}

.sobre, .mim {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 64px;
    color: rgb(255, 255, 255,.8);
    margin-top: 50px;
    margin-bottom: 24px;
    display: inline-block;
    padding: 0 5px ;
}

.sobre {
    color: #ffffff;
}

.mim {
    color: $cor-primaria;
}

.foto-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
    margin-bottom: -20px;
    position: relative; /* Adicione essa linha para posicionar o .texto-digitando de acordo com o .foto-container */
}

.foto {
    position: relative;
    height: 200px;
    text-align: center;
    margin-top: 10px; /* Espaçamento entre a foto e o texto abaixo */
    margin-bottom: 50px;
    box-shadow: 0px 0px 25px rgba(255, 92, 250, 0.218);
    border-radius: 500px;
}

.foto img {
    width: 200px;
    height: auto;
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
}

.foto-container:hover .texto-digitando {
    opacity: 1;
    animation: typing 2s steps(18, end);
}

.foto img:hover {
    transform: scale(1.1);
    filter: brightness(70%);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.6);
}

.foto-container .texto-digitando {
    position: absolute;
    top: 50%;
    left: 240px; /* Posição à esquerda da foto com um espaçamento de 40px */
    transform: translateY(-50%);
    width: 300px;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    opacity: 0;
    transition: opacity 0.3s ease, left 0.3s ease;
    font-style: italic;
    quotes: "\201C" "\201D" "\2018" "\2019";
    text-indent: 20px;
}

.foto-container:hover .texto-digitando {
    opacity: 1;
    left: calc(100% + 20px); /* Posição à direita da foto com um espaçamento de 20px */
    animation: typing 2s steps(18, end);
}   

.descricao-sobre {
    text-align: center;
    max-width: 800px;
    margin: 0 auto;

    p {
        color: whitesmoke;
        opacity: 0.8;
    }

    .texto-curto,
    .texto-completo {
        color: rgb(255, 255, 255, .8);
        text-align: justify;
        text-justify: inter-word;
        word-spacing: 0.2em;
        line-height: 1.5;
        font-size: 18px;
        padding: 0 15px; /* Adicione um espaçamento interno de 15px ao redor do texto */
    }
    
    .paragrafo-curto,
    .paragrafo-completo {
        margin-bottom: 20px; /* Espaçamento uniforme entre parágrafos */
    }
    

.mostrar-mais {
    background-color: transparent;
    border: 2px solid $cor-primaria;
    color: white;
    cursor: pointer;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin: 0 auto;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
}

.mostrar-mais .seta {
    position: relative;
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 15px solid white;
    transition: transform 0.3s ease;
}

.texto-completo.mostrar {
    padding: 15px;
}

.mostrar-mais:hover .seta {
    transform: translateY(5px);
    animation: setaAnimacao 0.6s infinite;
}

.mostrar-mais.rotacionar-cima .seta {
    transform: rotate(180deg) translateY(-5px);
}

@keyframes setaAnimacao {
    0%, 100% {
        opacity: 0.5;
    }
    50% {
        opacity: 1;
    }
}
 
}

/* Estilos para telas pequenas (celulares) */
@media (max-width: 767px) {
    .sobre-mim {
        margin-top: 50px;
    }

    #sobre-mim .sobre,
    #sobre-mim .mim {
    font-size: 36px; /* Tamanho do texto em telas de tamanho médio */
  }

    .foto-container .texto-digitando {
        left: 50%;
        transform: translateX(-50%);
        background-color: rgba(0, 0, 0, 0.641);
        width: 80%;
    }
}