.redes-sociais {
    position: fixed;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px; /* Espaçamento vertical entre os ícones */
    z-index: 999;
}

.redes-sociais a {
    color: #C961DE;
    opacity: 0.6;
    transition: all 0.4s ease;
}

.redes-sociais a:hover {
    opacity: 1;
    transform: scale(1.1);
}