@import '../../variables';

.servicos {
    color: $cor-primaria;
}

    .services-container {
        background-color: $cor-de-fundo-box;
        margin-bottom: 50px;
        height: 600px;
    }

#services, .services-heading {
    height: auto;
    width: 100%;
    text-align: center;
    font-size: 64px;
    font-family: 'Poppins', sans-serif;
    color: whitesmoke;
    padding: 10px;
    padding-bottom: 50px;
    padding-top: 20px;
    font-weight: 600;
    background-color: $cor-de-fundo-box;
    margin-top: 50px;

}

.services-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 2.5%;
    background-color: $cor-de-fundo-box; /* Cor de fundo para os containers internos */
}

.service {
    height: 340px;
    width: 25%;
    margin: 1.5%;
    background-color: #191919;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0px 0px 25px rgba(255, 92, 250, 0.218);
    border-radius: 10px;
    overflow: hidden;
    transition: all 0.3s ease;
    transform-origin: center bottom;
    position: relative;
}

.service:hover {
    transform: scale(1.1);
    transition: all .3s ease;
}

.service-img img {
    display: block;
    margin: 20px auto;
    border-radius: 30%;
    padding: 10px;
    background: $cor-de-fundo-hover;
    height: 100px;
    transition: all 1.2s ease;
}

.service:hover .service-img img{
    box-shadow:0px 0px 0px 400px $cor-de-fundo-hover ;
}

.service-description {
    font-family: 'poppins', sans-serif;
    color: whitesmoke;
    text-align: center;
    padding: 10px;
    position: relative;
    top: -50px;
}

.service-description h2 {
    margin: 10px 0;
    font-weight: bold;
    font-size: 28px;
}

.service-description p {
    font-weight: 300;
    font-size: 16px;
    opacity: .8;
}

/* Estilos para telas pequenas (celulares) */
@media (max-width: 767px) {
    #services .color {
        font-size: 36px; /* Diminuindo o tamanho do texto "Meus" em telas menores */
    }

    #services .servicos {
        font-size: 36px; /* Diminuindo o tamanho do texto "serviços" em telas menores */
    }

    .services-container {
        height: auto;
        padding-bottom: 30px;
    }

    .service {
        width: 80%;
        height: auto;
        margin: 8%;
    }

    .service-img img {
        height: 70px;
        width: 70px;
    }

    .service-description h2 {
        font-size: 24px;
        padding-top: 20px;
        top: 0;
    }
}
